.custom-file {
    overflow: hidden;
}

body {
    background-color: #eee
}

.card {
    border: none
}

.card-body {
    flex: 1 1 auto;
    padding: 10px
}

.card-text {
    font-size: 13px
}

.guarantee {
    color: #05882c;
    margin-left: 4px;
    font-weight: 700
}

hr {
    margin: 0.2rem 0;
    color: #bfbebe
}

.card-img-top {
    width: 100%;
    height: 15vw;
    object-fit: cover;
}